import { useFile } from "@rp/react/tanstack-api/file"
import { RPTooltip } from "@rp/react/web/common/base-components"
import { ShareIcon } from "@rp/react/web/common/base-components"
import { MaxWidthContainer } from "@rp/react/web/common/composite-components"
import { Box, Divider, IconButton } from "@rp/react/web/mui/core"
import { ArrowBack } from "@rp/react/web/mui/icons"
import { CustomDocumentHeaderProps } from "../../DocumentViewer.types"
import { HeaderContainer, HeaderContent, LeftContent, RightContent } from "./PDFViewerHeader.styles"
import { PDFViewerHeaderPaneVariant } from "./PDFViewerHeaderPaneVariant"
import {
  PDFViewHeaderPages,
  PDFViewerHeaderDownloadButton,
  PDFViewerHeaderZoomInButton,
  PDFViewerHeaderZoomOutButton,
} from "./components"
import { PDFViewerHeaderFilename } from "./components/PDFViewerHeaderFilename"

type PDFViewerHeaderProps = {
  isMobile: boolean
  backTitle: string
  handleClick: () => void
  name: string
  numPages: number
  currentPage: string | number
  scale: number
  setScale: React.Dispatch<React.SetStateAction<number>>
  documentId: string
  isPrivate: boolean
  disableBackButton?: boolean
  pdfHeaderOffset?: number
  customDocumentHeaderProps?: CustomDocumentHeaderProps
}

/**
 * Renders the header component for the PDF viewer.
 *
 * @param props - The component props.
 * @returns The rendered header component.
 */

function PDFViewerHeader({
  isMobile,
  backTitle,
  disableBackButton = false,
  handleClick,
  name,
  numPages,
  currentPage,
  scale,
  setScale,
  documentId,
  isPrivate,
  pdfHeaderOffset,
  customDocumentHeaderProps,
}: PDFViewerHeaderProps) {
  const { data } = useFile(isPrivate ? documentId : undefined)
  const showCompanyIcon = !!(data?.sharedWith && data?.sharedWith?.length > 0)

  if (customDocumentHeaderProps?.variant === "pane") {
    return (
      <PDFViewerHeaderPaneVariant
        name={name}
        numPages={numPages}
        currentPage={currentPage}
        scale={scale}
        setScale={setScale}
        documentId={documentId}
        isPrivate={isPrivate}
        pdfHeaderOffset={pdfHeaderOffset}
        customDocumentHeaderProps={customDocumentHeaderProps}
        data={data}
      />
    )
  }

  return (
    <HeaderContainer top={pdfHeaderOffset}>
      <MaxWidthContainer style={{ display: "flex", alignItems: "center" }}>
        <HeaderContent isMobile={isMobile}>
          <LeftContent>
            {isMobile || disableBackButton || window.history.length === 1 ? null : (
              <RPTooltip title={backTitle} arrow placement="top">
                <IconButton data-testid="back-arrow-button" onClick={handleClick}>
                  <ArrowBack />
                </IconButton>
              </RPTooltip>
            )}
            <PDFViewerHeaderFilename data={data} name={name} />
            {showCompanyIcon ? <ShareIcon shared sx={{ mx: 1 }} /> : null}
          </LeftContent>
          <RightContent>
            <PDFViewHeaderPages currentPage={currentPage} numPages={numPages} />

            {isMobile ? null : (
              <Divider
                orientation="vertical"
                flexItem
                color="#5E5E5E"
                sx={{ mx: 2, my: "auto", height: 24 }}
              />
            )}

            <Box display="flex" alignItems="center">
              <PDFViewerHeaderZoomOutButton scale={scale} setScale={setScale} />
              <PDFViewerHeaderZoomInButton scale={scale} setScale={setScale} />
              <PDFViewerHeaderDownloadButton
                isPrivate={isPrivate}
                documentId={documentId}
                name={name}
              />
            </Box>
          </RightContent>
        </HeaderContent>
      </MaxWidthContainer>
    </HeaderContainer>
  )
}

export { PDFViewerHeader }
